.navbar {
  background: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(24px) saturate(180%);
  border-bottom: 2px solid rgba(52, 152, 219, 0.12);
  box-shadow: 0 4px 32px rgba(52, 152, 219, 0.10), 0 1.5px 0 rgba(52, 152, 219, 0.08);
  transition: background 0.3s, box-shadow 0.3s;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 999;
}

.navbar.scrolled {
  background: rgba(255, 255, 255, 0.92);
  box-shadow: 0 8px 32px rgba(52, 152, 219, 0.18), 0 2px 0 rgba(52, 152, 219, 0.12);
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1200px;
  padding: 0 20px;
}

.navbar-logo {
  color: #2c3e50;
  text-decoration: none;
  font-size: 2rem;
  font-weight: 800;
  display: flex;
  align-items: center;
  transition: all 0.3s ease;
  position: relative;
  gap: 0.5rem;
}

.navbar-logo:hover {
  color: #3498db;
  transform: translateY(-1px);
}

.logo-text {
  color: #3498db;
  background: none;
  -webkit-background-clip: unset;
  -webkit-text-fill-color: unset;
  background-clip: unset;
  animation: none;
}

.navbar-avatar {
  width: 38px;
  height: 38px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 12px;
  border: 2.5px solid #3498db;
  box-shadow: 0 2px 8px rgba(52, 152, 219, 0.15);
  background: #fff;
  transition: box-shadow 0.3s, border 0.3s;
}

.nav-menu {
  display: flex;
  align-items: center;
  list-style: none;
  text-align: center;
  margin: 0;
  padding: 0;
  gap: 0.5rem;
}

.nav-item {
  height: 80px;
  display: flex;
  align-items: center;
  position: relative;
}

.nav-link {
  position: relative;
  display: flex;
  align-items: center;
  gap: 0.6rem;
  font-size: 1.08rem;
  color: #2c3e50;
  padding: 0 1.3rem;
  border-radius: 10px;
  transition: color 0.2s;
  overflow: hidden;
  z-index: 1;
  background: none;
  text-decoration: none;
  font-weight: 500;
}

.nav-link-icon {
  font-size: 1.15rem;
  color: #3498db;
  transition: color 0.2s;
}

.nav-link-label {
  position: relative;
  z-index: 2;
  font-weight: 500;
  text-decoration: none;
}

.nav-link::after {
  content: none;
  display: none;
}

.nav-link:hover, .nav-link.active {
  color: #3498db;
  background: none;
  box-shadow: none;
}

.nav-link:hover .nav-link-icon, .nav-link.active .nav-link-icon {
  color: #3498db;
}

.menu-icon {
  display: none;
  color: #2c3e50;
  font-size: 2rem;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 10px;
  background: rgba(52, 152, 219, 0.07);
  transition: background 0.2s, color 0.2s, box-shadow 0.2s;
}

.menu-icon:hover {
  color: #3498db;
  background: rgba(52, 152, 219, 0.18);
  box-shadow: 0 2px 8px rgba(52, 152, 219, 0.10);
}

/* Mobile Navigation */
@media screen and (max-width: 960px) {
  .nav-menu {
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 0;
    pointer-events: none;
    transition: left 0.5s cubic-bezier(0.4,0,0.2,1), opacity 0.4s;
    background: rgba(255, 255, 255, 0.98);
    backdrop-filter: blur(24px);
    box-shadow: 0 10px 30px rgba(52, 152, 219, 0.13);
    z-index: 10;
  }
  .nav-menu.active {
    left: 0;
    opacity: 1;
    pointer-events: auto;
    transition: left 0.5s cubic-bezier(0.4,0,0.2,1), opacity 0.4s;
  }
  .nav-item {
    height: auto;
    margin: 0.7rem 0;
    width: 100%;
  }
  .nav-link {
    justify-content: center;
    font-size: 1.15rem;
    padding: 1.1rem 2rem;
    border-radius: 14px;
    margin: 0 1.2rem;
  }
  .menu-icon {
    display: block;
  }
  .navbar-logo {
    font-size: 1.5rem;
  }
}

@media screen and (max-width: 480px) {
  .navbar {
    height: 70px;
  }
  .nav-menu {
    top: 70px;
    height: calc(100vh - 70px);
  }
  .navbar-logo {
    font-size: 1.2rem;
  }
  .navbar-avatar {
    width: 30px;
    height: 30px;
    margin-right: 7px;
  }
  .nav-link {
    font-size: 1rem;
    padding: 0.9rem 1.2rem;
  }
} 