.skills {
  background: linear-gradient(135deg, #ffffff 0%, #f8f9fa 100%);
  padding: 100px 0;
  position: relative;
  overflow: hidden;
  color: #2c3e50;
}

.skills::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="dots" width="20" height="20" patternUnits="userSpaceOnUse"><circle cx="10" cy="10" r="1" fill="%233498db" opacity="0.1"/></pattern></defs><rect width="100" height="100" fill="url(%23dots)"/></svg>');
  pointer-events: none;
}

.skills-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  position: relative;
  z-index: 1;
}

.skills-header {
  text-align: center;
  margin-bottom: 4rem;
  animation: fadeInUp 1s ease-out;
}

.skills-title {
  font-size: 3rem;
  font-weight: 700;
  color: #2c3e50;
  margin-bottom: 1rem;
  background: linear-gradient(45deg, #3498db, #2c3e50);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  position: relative;
}

.skills-title::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 4px;
  background: linear-gradient(45deg, #3498db, #2c3e50);
  border-radius: 2px;
}

.skills-subtitle {
  font-size: 1.2rem;
  color: #34495e;
  font-weight: 400;
  max-width: 600px;
  margin: 0 auto;
}

.skills-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 3rem;
  margin-bottom: 4rem;
}

.skill-category {
  background: white;
  backdrop-filter: none;
  border-radius: 20px;
  padding: 2.5rem;
  border: 1px solid rgba(52, 152, 219, 0.08);
  box-shadow: 0 5px 20px rgba(44,62,80,0.06);
  transition: all 0.3s ease;
  animation: fadeInUp 1s ease-out 0.2s both;
}

.skill-category:hover {
  background: #f8f9fa;
  border-color: #3498db33;
  box-shadow: 0 10px 30px rgba(52,152,219,0.10);
}

.skill-category:nth-child(2) {
  animation-delay: 0.4s;
}

.skill-category:nth-child(3) {
  animation-delay: 0.6s;
}

.category-header {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 2rem;
}

.category-icon {
  width: 50px;
  height: 50px;
  background: linear-gradient(45deg, #3498db, #2c3e50);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  color: white;
}

.category-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #2c3e50;
  margin: 0;
}

.category-description {
  font-size: 1rem;
  line-height: 1.6;
  color: #34495e;
  margin-bottom: 2rem;
}

.skills-list {
  display: flex;
  flex-wrap: wrap;
  gap: 0.7rem;
  margin-top: 1rem;
  margin-bottom: 1.5rem;
}

.skill-pill {
  background: transparent;
  color: #3498db;
  font-weight: 600;
  font-size: 1rem;
  padding: 0.5rem 1.2rem;
  border-radius: 30px;
  box-shadow: 0 2px 8px rgba(44,62,80,0.08);
  border: 2px solid #3498db;
  display: inline-block;
  letter-spacing: 0.5px;
  transition: background 0.2s, color 0.2s, border 0.2s, transform 0.2s;
  cursor: default;
}

.skill-pill:hover, .skill-pill:active {
  background: linear-gradient(45deg, #3498db, #2c3e50);
  color: white;
  border-color: #3498db;
  transform: translateY(-2px) scale(1.05);
}

.skill-item, .skill-header, .skill-bar, .skill-progress, .skill-percentage {
  display: none !important;
}

.skills-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 2rem;
  margin-top: 4rem;
  animation: fadeInUp 1s ease-out 0.8s both;
}

.stat-card {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  border-radius: 15px;
  padding: 2rem;
  text-align: center;
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease;
}

.stat-card:hover {
  transform: translateY(-5px);
  background: rgba(255, 255, 255, 0.15);
  border-color: rgba(255, 255, 255, 0.3);
}

.stat-number {
  font-size: 3rem;
  font-weight: 700;
  color: #3498db;
  margin-bottom: 0.5rem;
  display: block;
}

.stat-label {
  font-size: 1rem;
  color: rgba(255, 255, 255, 0.8);
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

/* Animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

/* Responsive Design */
@media screen and (max-width: 768px) {
  .skills {
    padding: 80px 0;
  }

  .skills-title {
    font-size: 2.5rem;
  }

  .skills-subtitle {
    font-size: 1.1rem;
  }

  .skills-content {
    grid-template-columns: 1fr;
    gap: 2rem;
  }

  .skill-category {
    padding: 2rem;
  }

  .category-title {
    font-size: 1.3rem;
  }

  .skills-stats {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
  }

  .stat-card {
    padding: 1.5rem;
  }

  .stat-number {
    font-size: 2.5rem;
  }
}

@media screen and (max-width: 480px) {
  .skills {
    padding: 60px 0;
  }

  .skills-title {
    font-size: 2rem;
  }

  .skills-subtitle {
    font-size: 1rem;
  }

  .skill-category {
    padding: 1.5rem;
  }

  .category-header {
    flex-direction: column;
    text-align: center;
    gap: 0.5rem;
  }

  .category-icon {
    width: 40px;
    height: 40px;
    font-size: 1.2rem;
  }

  .category-title {
    font-size: 1.2rem;
  }

  .skill-item {
    padding: 1rem;
  }

  .skill-name {
    font-size: 1rem;
  }

  .skills-stats {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .stat-card {
    padding: 1rem;
  }

  .stat-number {
    font-size: 2rem;
  }
} 